import axios from "axios";
axios.defaults.timeout = 5000; //5;
axios.defaults.withCredentials = true; //

// Content-Type 
// axios.defaults.headers.post["Content-Type"] =
//   "application/x-www-form-urlencoded;charset=UTF-8";
// axios.defaults.headers.post["Content-Type"] = "application/json";

const https = require("https"); //  Node.js  https 
//  HTTPS Agent
const httpsAgent = new https.Agent({
  rejectUnauthorized: false
});

// url
axios.defaults.baseURL = "https://localhost:8888";
// axios.defaults.baseURL = "https://8.148.6.61:8888";
axios.defaults.httpsAgent = httpsAgent; //  HTTPS Agent
// 
axios.interceptors.response.use(
  response => {
    // responsestatus200
    if ((response.status = 200)) {
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
  },
  error => {
    console.log(error);
    if (error.response.status) {
      switch (error.response.status) {
        // 
        case 401:
          router.replace({
            path: "/",
            query: {
              redirect: router.currentRoute.fullPath
            }
          });
          break;

        //
        case 404:
          break;
      }
      return Promise.reject(error.response);
    }
  }
);

/**
 * get
 */
export function get(url, params = {}) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, { params: params })
      .then(response => {
        resolve(response.data);
      })
      .catch(err => {
        reject(err);
      });
  });
}

/**
 * post
 */
export function post(url, data = {}) {
  return new Promise((resolve, reject) => {
    axios
      .post(url, data)
      .then(response => {
        resolve(response.data);
      })
      .catch(err => {
        reject(err);
      });
  });
}



